<template>
  <div>
    <h2>Add New Dokter</h2>
    <form @submit.prevent="addDokter">
      <div>
        <label for="nama">Nama:</label>
        <input type="text" id="nama" v-model="newDokter.nama" required>
      </div>
      <div>
        <label for="spesialisasi">Spesialisasi:</label>
        <input type="text" id="spesialisasi" v-model="newDokter.spesialisasi" required>
      </div>
      <div>
        <label for="alamat">Alamat:</label>
        <input type="text" id="alamat" v-model="newDokter.alamat" required>
      </div>
      <div>
        <label for="telepon">Telepon:</label>
        <input type="text" id="telepon" v-model="newDokter.telepon" required>
      </div>
      <button type="submit" class="add-button">Add Dokter</button>
    </form>
    <h1>Dokter List</h1>
    <table>
      <thead>
        <tr>
          <th>Nama</th>
          <th>Spesialisasi</th>
          <th>Alamat</th>
          <th>Telepon</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="dokter in dokterList" :key="dokter.id">
          <td>{{ dokter.nama }}</td>
          <td>{{ dokter.spesialisasi }}</td>
          <td>{{ dokter.alamat }}</td>
          <td>{{ dokter.telepon }}</td>
          <td>
            <button @click="deleteDokter(dokter.id)" class="delete-button">Delete</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      dokterList: [],
      newDokter: {
        nama: '',
        spesialisasi: '',
        alamat: '',
        telepon: '',
      },
    };
  },
  mounted() {
    this.fetchDokterList();
  },
  methods: {
    fetchDokterList() {
      axios
        .get('http://localhost:5000/dokter')
        .then((response) => {
          this.dokterList = response.data;
        })
        .catch((error) => {
          console.error('Error fetching dokter list:', error);
        });
    },
    addDokter() {
      axios
        .post('http://localhost:5000/dokter', this.newDokter)
        .then((response) => {
          console.log(response.data);
          this.fetchDokterList();
          this.resetForm();
        })
        .catch((error) => {
          console.error('Error adding dokter:', error);
        });
    },
    deleteDokter(id) {
      axios
        .delete(`http://localhost:5000/dokter/${id}`)
        .then((response) => {
          console.log(response.data);
          this.fetchDokterList();
        })
        .catch((error) => {
          console.error('Error deleting dokter:', error);
        });
    },
    resetForm() {
      this.newDokter = {
        nama: '',
        spesialisasi: '',
        alamat: '',
        telepon: '',
      };
    },
  },
}
</script>

<style>
h1 {
  color: #333;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  border: 1px solid #ccc;
  padding: 8px;
}

form {
  margin-top: 20px;
}

form div {
  margin-bottom: 10px;
}

.add-button {
  background-color: #1976d2;
  color: white;
  padding: 8px 16px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.delete-button {
  background-color: #f44336;
  color: white;
  padding: 8px 16px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}
</style>
