import MasterLayout from './../pages/layout/master';
import Dashboard from './../pages/dashboard';
import User from './../pages/user';
import Product from './../pages/product';
import Contact from './../pages/contact';
import Customer from './../pages/customer';
import Activation from './../pages/activation';

export default [
  {
    name: "Master",
    path: "/",
    component: MasterLayout,
    redirect: '/dashboard',
    children: [
      {
        name: 'dashboard',
        path: '/dashboard',
        component: Dashboard,
      },
      {
        name: 'user',
        path: '/user',
        component: User,
      },
      {
        name: 'product',
        path: '/product',
        component: Product,
      },
      {
        name: 'contact',
        path: '/contact',
        component: Contact,
      },
      {
        name: 'customer',
        path: '/customer',
        component: Customer,
      },
      {
        name: 'activation',
        path: '/activation',
        component: Activation,
      }
    ]
  },
];
